/**
 * @file 预约单详情
 */
@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';

.reservation_detail_container {
  padding-top: pr2(16);
  >div {
    padding: 0 pr2(20) pr2(34) pr2(20);
  }
  &.btn_fixed_bottom {
    padding-bottom: 5.25rem;
    padding-bottom: calc(5.25rem + constant(safe-area-inset-bottom));
    padding-bottom: calc(5.25rem + env(safe-area-inset-bottom));
  }
  .reservation_state {
    font-size: pr2(22);
    font-family: PingFangSC-Medium;
    font-weight: 700;
    color: rgba(255,102,0,1);
    line-height: 1;
    margin-bottom: pr2(16);
    box-sizing: border-box;
    &.order_finished {
      color: #0055FF;
    }
    &.order_cancel {
      color: #666D7F;
    }
  }
  .reservation_process {
    width: 100%;
    height: pr2(36);
    background: rgba(248,249,252,1);
    border-radius: pr2(4);
    padding: 0 pr2(20);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    position: relative;
    .point {
      width: pr2(5);
      height: pr2(5);
      border-radius: 50%;
      background: rgba(0, 204, 136, 1);
      margin-right: pr2(8);
    }
    .process_content {
      font-size: pr2(12);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(17,30,54,1);
      line-height: 1;
    }
    .right_arrow {
      width: pr2(8);
      height: pr2(8);
      background: url('data:image/png;base64,') no-repeat;
      background-size: 100% 100%;
      position: absolute;
      right: pr2(20);
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .common_card_container {
    width: 100%;
    border-radius: pr2(4);
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17,30,54,0.06);
    margin: pr2(12) 0;
    .card_wrapper {
      width: 100%;
      min-height: pr2(50);
      padding: pr2(17) pr2(20);
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &.location_container {
        align-items: flex-start;
      }
      .card_wrapper_title {
        font-size: pr2(16);
        font-family: PingFangSC-Medium;
        font-weight: 700;
        color: rgba(17,30,54,1);
        line-height: 1;
        margin-right: pr2(24);
      }
      .card_wrapper_content {
        font-size: pr2(16);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(17,30,54,1);
        line-height: 1;
        text-align: right;
        flex: 1;
        @include ellipsis();
        .mileage {
          font-size: pr2(18);
          font-family: YCZJ-DINAlternate;
          color: rgba(17,30,54,1);
          line-height: 1;
        }
      }
      .loaction_wrapper {
        font-size: pr2(16);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(17,30,54,1);
        line-height: 1;
        text-align: right;
        display: flex;
        justify-content: flex-end;
        position: relative;
        .location_icon {
          width: pr2(16);
          height: pr2(16);
          background: url('data:image/png;base64,') no-repeat;
          background-size: 100% 100%;
          margin: pr2(2) pr2(5) 0 0;
        }
        >p:last-of-type {
          max-width: pr2(176);
          @include ellipsisLn(3);
          line-height: pr2(20);
        }
      }
    }
  }
  .service_wrapper {
    width: 100%;
    height: pr2(76);
    padding: 0 pr2(20);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17,30,54,0.06);
    border-radius: pr2(4);
    position: relative;
    .portrait_wrapper {
      width: pr2(50);
      height: pr2(50);
      border-radius: 50%;
      margin-right: pr2(12);
      >img {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .middle_wrapper {
      height: pr2(50);
      display: flex;
      flex-direction: column;
      justify-content: center;
      >p:first-of-type  {
        font-size: pr2(16);
        font-family: PingFangSC-Medium;
        font-weight: 700;
        color: rgba(17,30,54,1);
        line-height: 1;
        // margin: pr2(3) 0 pr2(12) 0;
      }
      >p:nth-of-type(2) {
        font-size: pr2(14);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(102,109,127,1);
        line-height: 1;
      }
    }
    .contact_Service_btn {
      width: pr2(76);
      height: pr2(32);
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: pr2(14);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(0,85,255,1);
      line-height: 1;
      position: absolute;
      right: pr2(20);
      top: 50%;
      transform: translateY(-50%);
      &:after {
        content: '';
        position: absolute;
        width: 200%;
        height: 200%;
        top: 0;
        left: 0;
        border: 1px solid rgba(0, 85, 255, 0.2);
        transform: scale(0.5);
        transform-origin: 0 0;
        box-sizing: border-box;
        border-radius: 4px;
      }
    }
  }
  .book_item_wrapper {
    width: 100%;
    border-radius: pr2(4);
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17,30,54,0.06);
    padding: 0 pr2(20) 0 pr2(20);
    box-sizing: border-box;
    margin-bottom: pr2(12);
    .item_text {
      height: pr2(50);
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: pr2(18);
      font-family: PingFangSC-Medium;
      font-weight: 700;
      color: rgba(17,30,54,1);
      line-height: 1;
      >p:first-of-type {
        flex: 1;
        position: relative;
        @include ellipsis()
      }
      >p:nth-of-type(2) {
        font-size: pr2(18);
        font-family: YCZJ-DINAlternate;
        font-weight: 600;
        color: rgba(255,102,0,1);
        line-height: 1;
      }
    }
    .show_more_btn {
      width: 100%;
      height: pr2(40);
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        font-size: pr2(12);
        font-family: PingFangSC-Medium;
        font-weight: 500;
        color: rgba(0,85,255,1);
        line-height: 1;
      }
      p:last-of-type {
        width: pr2(7);
        height: pr2(4);
        background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAICAYAAADJEc7MAAAAiElEQVQoU53Ozw2CUAzH8e+Pm3fZQJbwxgxG9jBxBe+sASaOwNUhHEHOcMKax38UEsJLmjRtP+0TG596F9kV4wZ8gKqNLp/WRDmGGUa49gMDdCKyN8Z+BQ6msMEVhreIPU4kevxDJ85ms1DEpLq43hLcAcUPfnLXsavNw+bqAXjVgyInlT9e9AVyoyUCvN9QSgAAAABJRU5ErkJggg==') no-repeat;
        background-size: 100% 100%;
        margin-left: pr2(4);
      }
      .arrow_up {
        transform: rotate(180deg);
      }
    }
    .item_wrapper {
      padding-bottom: pr2(8);
      box-sizing: border-box;
      p {
        line-height: pr2(20);
        font-size: pr2(16);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(17,30,54,1);
        border-radius: pr2(4);
        display: flex;
        align-items: center;
      }
    }
  }
  .order_info_wrapper {
    width: 100%;
    border-radius: pr2(4);
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17,30,54,0.06);
    padding: 0 pr2(20);
    margin-top: pr2(12);
    box-sizing: border-box;
    margin-bottom: pr2(12);
    .order_text_wrapper {
      height: pr2(62);
      padding-top: pr2(17);
      box-sizing: border-box;
      >p:first-of-type {
        font-size: pr2(18);
        font-family: PingFangSC-Medium;
        font-weight: 700;
        color: rgba(17,30,54,1);
        line-height: 1;
        margin-bottom: pr2(12);
      }
      >p:nth-of-type(2) {
        font-size: pr2(12);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(102,109,127,1);
        line-height: 1;
        margin-bottom: pr2(5);
      }
    }
    .order_info_common_wrapper {
      height: pr2(50);
      display: flex;
      align-items: center;
      justify-content: space-between;
      &.order_num_wrapper {
        height: pr2(50);
        justify-content: flex-start;
      }
    }
    .order_info_common_wrapper {
      font-size: pr2(16);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(17,30,54,1);
      line-height: 1;
      >p:first-of-type {
        max-width: pr2(70);
      }
      >p:nth-of-type(2) {
        flex: 1;
        text-align: right;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .order_num_wrapper {
        height: pr2(50);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex: 1;
        .input_wrapper {
          height: pr2(50);
          position: relative;
          display: flex;
          align-items: center;
          .copy_target {
            display: block;
            height: pr2(50);
            text-align: right;
            padding: 0;
          }
          &:after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 998;
            height: pr2(50);
          }
        }
        .copy_target {
          flex: 1;
          display: block;
          border: 0px;
          -webkit-appearance: none;
          background: none;
          outline: none;
          padding: 0;
          text-align: right;
          width: pr2(160);
          height: pr2(50);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          position: relative;
        }
        .copy_btn {
          height: pr2(50);
          font-size: pr2(14);
          font-family: PingFangSC-Regular;
          font-weight: 400;
          color: rgba(0,85,255,1);
          line-height: 1;
          margin-left: pr2(12);
          display: flex;
          align-items: center;
        }
      }
    }
  }
  .btn_wrapper {
    width: 100%;
    height: 4.125rem;
    height: calc(4.125rem + constant(safe-area-inset-bottom));
    height: calc(4.125rem + env(safe-area-inset-bottom));
    padding: 0 pr2(20);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    background:rgba(255,255,255,1);
    box-shadow: 0 pr2(-6) pr2(16) 0 rgba(0,0,0,0.04);
    position: relative;
    &.btn_fixed_bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 998;
    }
    .view_process_btn, .cancel_book_btn {
      width: pr2(375);
      height: pr2(50);
      border-radius: pr2(6);
      font-size: pr2(16);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(255,255,255,1);
      line-height: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      width: pr2(335);
      position: absolute;
      top: pr2(8);
      left: 50%;
      transform: translateX(-50%);

    }
    .cancel_book_btn {
      width: pr2(335);
      position: absolute;
      top: pr2(8);
      left: 50%;
      transform: translateX(-50%);
    }
    .finished_btn_wrapper {
      width: 100%;
      height: pr2(66);
      background: #fff;
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: pr2(8);
      left: 0;
      padding: 0 pr2(20);
      z-index: 998;
      box-sizing: border-box;
      .view_detail_btn, .evaluate_btn {
        width: 48.7%;
        height: pr2(50);
        border-radius: pr2(6);
        font-size: pr2(16);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(255,255,255,1);
        line-height: 1;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .view_detail_btn {
        background: rgba(0, 204, 136, 1);
      }
    }
    .view_process_btn, .cancel_book_btn, .evaluate_btn {
      background: rgba(0, 85, 255, 1);
    }
    .evaluate_btn_only {
      width: 100%!important;
    }
  }
}